<template>
   <div class="userManage">
       <el-button type="success" @click="regist">注册新用户</el-button>
       <div class="form">
            <div class="form-item">
                用户名：
                <el-input placeholder="输入用户名" class="input" v-model="userNameSearch">
                </el-input>
            </div>
            <div class="form-item">
                注册日期：
                <el-date-picker
                v-model="registTime"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择日期">
                </el-date-picker>
            </div>
            <el-button type="success" @click="search">搜索</el-button>
            <div class="table">
                <el-table
                :data="list"
                style="width: 100%">
                <el-table-column
                    prop="userName"
                    label="用户名"
                    width="180">
                </el-table-column>
                <el-table-column
                    prop="password"
                    label="密码"
                    width="180">
                </el-table-column>
                <el-table-column
                    label="用户类型"
                    width="180">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px" v-if="+scope.row.type === 1">管理员</span>
                        <span style="margin-left: 10px" v-if="+scope.row.type === 2">c段用户</span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="money"
                    label="剩余分数">
                </el-table-column>
                <el-table-column
                    prop="createtime"
                    label="注册时间">
                </el-table-column>
                <el-table-column
                    label="状态">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px" v-if="+scope.row.state === 1">正常</span>
                        <span style="margin-left: 10px" v-if="+scope.row.state === 2">封号中</span>
                        <span style="margin-left: 10px" v-if="+scope.row.state === 3">删除</span>
                    </template>
                </el-table-column>
                <el-table-column
                width="300"
                    label="操作">
                    <template slot-scope="scope">
                        <el-button type="success" @click="action(1, scope.row)" plain v-if="+scope.row.state !== 2">上分</el-button>
                        <el-button type="warning" @click="action(-1, scope.row)" plain v-if="+scope.row.state !== 2">下分</el-button>
                        <el-button type="danger" @click="action(3, scope.row)" plain v-if="+scope.row.state !== 2">封号</el-button>
                    </template>
                </el-table-column>
                </el-table>
            </div>
            <div class="pageNation">
                <el-pagination
                @current-change="handleCurrentChange"
                :current-page.sync="pageNum"
                :page-size="pageSize"
                layout="prev, pager, next, jumper"
                :total="total">
                </el-pagination>
            </div>
        </div>
   </div>
</template>

<script>
import {
    post,
    REGEST_NEW_USER,
    GET_LIST_USER,
    USER_LIST_ACTION,
    USER_FORBIDDEN,
} from '../../http';

export default {
    name: 'userManage',
    data() {
        return {
            registTime: '',
            pageSize: 10,
            pageNum: 1,
            list: [{
                money: 100,
            }],
            userNameSearch: '', // 搜索用的用户名
            total: 0,
        };
    },
    mounted() {
        this.initList();
    },
    methods: {
        // 生成用户名密码
        async regist() {
            const { userName, password } = await post(REGEST_NEW_USER);
            this.$confirm(`账号：${userName}    密码：${password}`, '新用户生成完毕', {
                showCancelButton: false,
                closeOnClickModal: false,
            });
        },
        // 列表接口
        async initList(params) {
            const { list, count } = await post(GET_LIST_USER, {
                pageSize: this.pageSize,
                pageNum: this.pageNum,
                ...params,
            });
            this.list = list;
            this.total = count;
        },
        async search() {
            const param = {
                userName: this.userNameSearch,
                date: this.registTime,
            };
            await this.initList(param);
        },
        handleCurrentChange(val) {
            this.pageNum = val;
            this.search();
        },
        // 操作
        action(type, row) {
            const text = +type === 1 ? '上分' : '下分';
            // 上分1 下分 -1
            if (+type === 1 || +type === -1) {
                this.$prompt(`请输入要对${row.userName}执行的${text}金额`, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                }).then(async ({ value }) => {
                    await post(USER_LIST_ACTION, {
                        type,
                        money: value,
                        userId: row.id,
                    });
                    this.initList();
                    this.$message({
                        type: 'success',
                        message: `${row.userName}，${text}成功！`,
                    });
                }).catch((e) => {
                    this.$message({
                        type: 'info',
                        message: e || '取消',
                    });
                });
            }
            // 封号操作
            if (+type === 3) {
                this.$confirm(`确认对${row.userName}进行封号操作吗？！`, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                }).then(async () => {
                    await post(USER_FORBIDDEN, {
                        userId: row.id,
                    });
                    this.initList();
                    this.$message({
                        type: 'success',
                        message: `${row.userName}，封号成功！`,
                    });
                }).catch((e) => {
                    this.$message({
                        type: 'info',
                        message: e || '取消',
                    });
                });
            }
        },
    },
};
</script>

<style lang="scss">
.userManage{
    .form{
        margin-top: 20px;
        .form-item{
            display: inline-block;
            margin-right: 20px;
            .input{
                width: 200px;
            }
        }
    }
}
</style>
