<template>
  <div class="login">
    <van-cell-group class="loginform">
        <van-field
            v-model="userName"
            label="用户名"
            left-icon="contact"
        />
        <van-field
            v-model="passWord"
            label="密码"
            left-icon="eye-o"
        />
    </van-cell-group>
    <van-row type="flex" justify="center" class="loginbtn">
        <van-button type="primary" block @click="login">登陆</van-button>
    </van-row>
  </div>
</template>

<script>
import {
    post,
    LOGIN,
    GET_USER_INFO,
} from '../http';

export default {
    name: 'login',
    data() {
        return {
            userName: '',
            passWord: '',
        };
    },
    methods: {
        async login() {
            try {
                // 登陆
                await post(LOGIN, {
                    name: this.userName,
                    pw: this.passWord,
                });
                // 拿取用户信息
                const { user } = await post(GET_USER_INFO);
                if (+user.type === 1) {
                    this.$router.push('mnageHome');
                    return;
                }
                this.$router.push('home');
            } catch (error) {
                this.$toast(error);
            }
            // console.log(data);
        },
    },
};
</script>

<style lang="scss">
.login{
    .loginform{
        width: 80%;
        margin: 0 auto;
        margin-top: 30%;
        border: 1px solid #07c160;
    }
    .loginbtn{
        width: 80%;
        margin: 0 auto;
        margin-top: 20px;
    }
}
</style>
