import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home';
import Login from '../views/Login';
import BeijingSaiChe from '../views/BeijingSaiChe';
import MnageHome from '../views/manage/MnageHome';
import userManage from '../views/manage/userManage';
import incomes from '../views/manage/incomes';
import messages from '../views/manage/messages';

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        name: 'login',
        component: Login,
    },
    {
        path: '/login',
        name: 'login',
        component: Login,
    },
    {
        path: '/home',
        name: 'home',
        component: Home,
    },
    {
        path: '/beijingsaiche',
        name: 'BeijingSaiChe',
        component: BeijingSaiChe,
    },
    {
        path: '/404',
        name: '404',
        component: {
            template: '<h1>您无权访问该页面</h1>',
        },
    },
    {
        path: '/MnageHome',
        name: 'MnageHome',
        redirect: '/MnageHome/userManage',
    },
    {
        path: '/MnageHome/:id',
        name: 'MnageHome',
        component: MnageHome,
        children: [{
            path: '/MnageHome/userManage',
            name: 'userManage',
            component: userManage,
        }, {
            path: '/MnageHome/incomes',
            name: 'incomes',
            component: incomes,
        }, {
            path: '/MnageHome/messages',
            name: 'messages',
            component: messages,
        }],
    },
    //   {
    //     path: '/about',
    //     name: 'about',
    //     // route level code-splitting
    //     // this generates a separate chunk (about.[hash].js) for this route
    //     // which is lazy-loaded when the route is visited.
    //     component: () => import(/* webpackChunkName: 'about' */ '../views/About.vue'),
    //   },
];

const router = new VueRouter({
    routes,
});

router.beforeEach((to, from, next) => {
    next();
});

export default router;
