<template>
  <div class="home">
    <van-notice-bar
    color="#1989fa"
    background="#ecf9ff"
    left-icon="info-o"
    >
    欢迎您，xxxx,
    </van-notice-bar>
    <div class="panel">
        <van-tag type="success">操作</van-tag>
        <van-row class="action">
            <van-col span="12">
                <div class="caozuo" @click="addMoney">
                    <van-icon name="gold-coin-o" size="40" />
                    <span>充值</span>
                </div>
            </van-col>
            <van-col span="12">
                <div class="caozuo" @click="tiXian">
                    <van-icon name="bag-o" size="40" />
                    <span>提现</span>
                </div>
            </van-col>
        </van-row>
    </div>
    <div class="panel">
        <van-tag type="success">热门游戏</van-tag>
        <van-row class="action">
            <van-col span="12" @click="jump('BeijingSaiChe')">
                <div class="caozuo">
                    <img src="https://www.5801133.com/Storage/Logo/10001.png" alt="" class="caizhong">
                    <span>北京赛车</span>
                </div>
            </van-col>
        </van-row>
    </div>
    <!--上分弹窗-->
    <van-popup
    v-model="showAdd"
    position="top"
    closeable
    :style="{ height: '70%' }"
    >
        <img src="//pic1.58cdn.com.cn//bizmp/n_v243f931214c164df69a51f0fb0dc45130_fbd2c721a0be1384.jpg?w=500" alt="" class="addImg">
        <div class="addBox">
            <van-field v-model="moneyAdd" placeholder="请输入上分金额" class="addinput"/>
            <van-button type="primary" plain @click="moneyAction(1)">提交</van-button>
        </div>
    </van-popup>
    <!--下分弹窗-->
    <van-popup
    v-model="showMines"
    position="top"
    closeable
    :style="{ height: '30%' }"
    >
        <div class="addBox tixianBox">
            <van-field v-model="moneyTixian" placeholder="请输入提现金额" class="addinput"/>
            <van-button type="primary" plain  @click="moneyAction(-1)">提交</van-button>
        </div>
    </van-popup>
  </div>
</template>

<script>
import {
    post,
    MONEY_ACTION,
} from '../http';

export default {
    name: 'home',
    data() {
        return {
            showAdd: false, // 充值弹窗，
            showMines: false, // 提现弹窗
            moneyAdd: '',
            moneyTixian: '',
        };
    },
    methods: {
        jump(url) {
            this.$router.push(url);
        },
        addMoney() {
            this.moneyAdd = '';
            this.showAdd = true;
        },
        tiXian() {
            this.moneyTixian = '';
            this.showMines = true;
        },
        async moneyAction(type) {
            try {
                const money = +type === 1 ? this.moneyAdd : this.moneyTixian;
                const tips = +type === 1 ? `申请充值${money}成功` : `申请提现${money}成功`;
                await post(MONEY_ACTION, {
                    type,
                    money,
                });
                this.showAdd = false;
                this.showMines = false;
                this.$toast(tips);
            } catch (error) {
                this.$toast(error);
            }
        },
    },
};
</script>

