<template>
    <div class="messages">
        <div class="form">
            <div class="form-item">
                用户名：
                <el-input placeholder="输入用户名" class="input" v-model="userNameSearch">
                </el-input>
            </div>
            <div class="form-item">
                时间：
                <el-date-picker
                v-model="timeSearch"
                type="daterange"
                range-separator="至"
                value-format="yyyy-MM-dd"
                start-placeholder="开始日期"
                end-placeholder="结束日期">
                </el-date-picker>
            </div>
            <div class="form-item">
                处理状态：
                <el-select v-model="state" placeholder="请选择">
                    <el-option
                    label="全部"
                    value="">
                    </el-option>
                    <el-option
                    label="未处理"
                    value="0">
                    </el-option>
                    <el-option
                    label="已处理"
                    value="1">
                    </el-option>
                </el-select>
            </div>
            <el-button type="success" @click="initList">搜索</el-button>
            <div class="table">
                <el-table
                :data="list"
                style="width: 100%">
                <el-table-column
                    prop="userName"
                    label="用户名"
                    width="180">
                </el-table-column>
                <el-table-column
                    label="处理状态"
                    width="180">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px" v-if="+scope.row.state === 1">已处理</span>
                        <span style="margin-left: 10px; color: red" v-if="+scope.row.state === 0">待处理</span>
                    </template>
                </el-table-column>
                <el-table-column
                    label="申请类型"
                    width="180">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px" v-if="+scope.row.type === 1">上分</span>
                        <span style="margin-left: 10px" v-if="+scope.row.type === -1">下分</span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="money"
                    label="操作分数">
                </el-table-column>
                <el-table-column
                    prop="createtime"
                    label="操作时间">
                </el-table-column>
                <el-table-column
                    prop="createtime"
                    label="操作">
                    <template slot-scope="scope">
                        <el-button type="danger" plain v-if="+scope.row.state === 0" @click="agreeAction(scope.row.id)">同意</el-button>
                    </template>
                </el-table-column>
                </el-table>
            </div>
            <div class="pageNation">
                <el-pagination
                @current-change="handleCurrentChange"
                :current-page.sync="pageNum"
                :page-size="pageSize"
                layout="prev, pager, next, jumper"
                :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
import {
    post,
    MESSAGE_LIST,
    MESSAGE_AGREE,
} from '../../http';

export default {
    name: 'messages',
    data() {
        return {
            userNameSearch: '',
            state: '',
            pageSize: 10,
            pageNum: 1,
            total: 0,
            list: [],
            timeSearch: [],
        };
    },
    mounted() {
        this.initList();
    },
    methods: {
        async initList() {
            const { list, count } = await post(MESSAGE_LIST, {
                pageSize: this.pageSize,
                pageNum: this.pageNum,
                userName: this.userNameSearch,
                start: this.timeSearch[0] || '',
                end: this.timeSearch[1] || '',
                state: this.state,
            });
            this.list = list;
            this.total = count;
        },
        handleCurrentChange(val) {
            this.pageNum = val;
            this.initList();
        },
        async agreeAction(id) {
            try {
                await post(MESSAGE_AGREE, {
                    cashId: id,
                });
                this.$message({
                    type: 'success',
                    message: '处理成功！',
                });
                this.initList();
            } catch (error) {
                this.$message({
                    type: 'info',
                    message: error,
                });
            }
        },
    },
};
</script>

<style lang="scss">
.messages{
    .form{
        margin-top: 20px;
        .form-item{
            display: inline-block;
            margin-right: 20px;
            .input{
                width: 200px;
            }
        }
    }
}
</style>
