export const derangedArray = (arr) => {
    const len = arr.length;
    const random = parseInt(Math.random() * len);
    const randomNum = arr[random];
    if (random - 1 < 0) {
        return arr;
    }
    arr[random] = arr[random - 1];
    arr[random - 1] = randomNum;
    return arr;
};

export const a = 10;
