import axios from 'axios';

/* eslint-disable */
const ajax = (url, init, cfg = {}) => new Promise((resolve, reject) => {
    console.log('.......>', init, url, this);
    const {
        dataType = 'json',
            isFullurl, other,
            timeout = 10000,
    } = cfg;
    // 请求开始，挂在loading组件
    // $loading(true);
    const fullurl = isFullurl ? url : url + (~url.indexOf('?') ? '&' : '?') + (+new Date()).toString(36);
    // const conf = Object.assign({ credentials: 'same-origin' }, init);
    const to = setTimeout(() => {
        reject(new Error('timeout....'));
    }, timeout);
    // axios方式，解决fetch兼容
    const { method, body } = init;
    if (method === 'get') {
        axios.get(fullurl)
            .then((response) => {
                clearTimeout(to);
                // 请求结束，loading消失
                // $loading(false);
                if (dataType !== 'json' || other) {
                    resolve(response.data);
                    return;
                }
                const { code, msg } = response.data;
                if (+code === 0) {
                    resolve(response.data.data);
                }else {
                    // reject(new Error(msg));
                    reject(msg);
                }
            })
            .catch((e) => {
                // 请求结束，loading消失
                // $loading(false);
                clearTimeout(to);
                reject(new Error(e));
            });
    } else {
        axios.post(fullurl, body, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                },
            })
            .then((response) => {
                // 请求结束，loading消失
                // $loading(false);
                clearTimeout(to);
                if (dataType !== 'json' || other) {
                    resolve(response.data);
                    return;
                }
                const { code, msg } = response.data;
                if (+code === 0) {
                    resolve(response.data.data);
                } else {
                    // reject(new Error(msg));
                    reject(msg);
                }
            })
            .catch((e) => {
                // 请求结束，loading消失
                // $loading(false);
                clearTimeout(to);
                reject(new Error(e));
            });
    }
});

export const formatParams = (params) => {
    const queryArr = [];
    Object.keys(params).forEach((key) => {
        const value = typeof params[key] === 'string' ? params[key] : JSON.stringify(params[key]);
        queryArr.push(`${key}=${value}`);
    });
    return queryArr.join('&');
};

const transFormData = (params) => {
    const fd = new FormData();
    Object.keys(params).forEach((key) => {
        fd.append(key, typeof params[key] === 'string' ? params[key] : JSON.stringify(params[key]));
    });
    return fd;
};

export const get = (url, params = {}, cfg) => ajax(
    url + (~url.indexOf('?') ? '&' : '?') + formatParams(params), { method: 'get' },
    cfg,
);

export const post = (url, params = {}, cfg) => ajax(
    url, { method: 'post', body: transFormData(params) },
    cfg,
);

export const jsonp = (...props) => new Promise((resolve, reject) => {
    console.log('props:', props);
    let url = props[0];
    const id = `jsonp${(+new Date()).toString(36)}`;
    if (props.length === 3) {
        url += (~url.indexOf('?') ? '&' : '?') + formatParams(props[1]);
    }
    url += `${(~url.indexOf('?') ? '&' : '?')}callback=${id}`;
    // const cb = props.length === 3 ? props[2] : props[1];

    const HEAD_DOM = document.getElementsByTagName('head')[0];
    const scriptNode = document.createElement('script');
    scriptNode.type = 'text/javascript';
    scriptNode.src = url;
    scriptNode.id = `id-${id}`;

    const callback = (data) => {
        console.log('data:', data);
        HEAD_DOM.removeChild(scriptNode);
        delete window[id];
        resolve(data);
        // cb && cb(data);
    };
    window[id] = data => callback(data);
    scriptNode.onerror = () => reject(new Error({ error: 'error' }));

    HEAD_DOM.appendChild(scriptNode);
});
